:root {
  --foreground-color: #444;
  --background-color: #fff;
  --title-font-size: 5em;
  --subtitle-font-size: 2em;
  --link-font-size: 1em;
}

@media (width <= 400px) {
  :root {
    --title-font-size: 4rem;
    --link-font-size: 1.25em;
  }
}

* {
  margin: 0;
  padding: 0;
}

.background-canvas {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
}

.container {
  background: var(--background-color);
  width: 60%;
  max-width: 400px;
  margin: 20vh auto;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
}

@media (width <= 400px) {
  .container {
    width: 100%;
    max-width: 100%;
  }
}

.container .title {
  text-align: center;
  font-size: var(--title-font-size);
  color: var(--foreground-color);
  margin-bottom: 10px;
}

.container .subtitle {
  text-align: center;
  font-size: var(--subtitle-font-size);
  color: var(--foreground-color);
  margin-bottom: 30px;
}

.container .nav .links-list {
  list-style: none;
  display: flex;
}

.container .nav .links-list .links-list-item {
  text-align: center;
  cursor: pointer;
  flex: 1;
  margin: 0 10px;
  padding: 10px 0;
  position: relative;
}

.container .nav .links-list .links-list-item .link {
  font-size: var(--link-font-size);
  color: var(--foreground-color);
  text-decoration: none;
}

.container .nav .links-list .links-list-item .link:before, .container .nav .links-list .links-list-item .link:after {
  content: "";
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: all .2s ease-out;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.container .nav .links-list .links-list-item .link:before {
  border-top: 1px solid var(--foreground-color);
  transform: translateY(50%);
}

.container .nav .links-list .links-list-item .link:after {
  border-bottom: 1px solid var(--foreground-color);
  transform: translateY(-50%);
}

.container .nav .links-list .links-list-item .link:hover:before, .container .nav .links-list .links-list-item .link:hover:after {
  opacity: 1;
  transform: translateY(0%);
}

